<template>
    <div>
        <br/>
        <title-with-sharp :title="$t('community.uc.credit_history')"/>
        <v-alert outlined type="info">
            {{$t('community.uc.cr.tip')}}
        </v-alert>
        <div>
            <v-card elevation="25" outlined flat tile>
                <v-data-table
                    :headers="headers"
                    :items="creditHistory"
                    sort-by="calories"
                >
                    <template v-slot:top>
                    <v-toolbar flat>
                        
                        <v-dialog
                        v-model="dialog"
                        max-width="500px"
                        >
                        <v-card>
                            <v-card-title>
                            <span class="text-h5">{{ formTitle }}</span>
                            </v-card-title>

                            <v-card-text>
                            <v-container>
                                Hello World
                            </v-container>
                            </v-card-text>

                            <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn
                                color="blue darken-1"
                                text
                                @click="close"
                            >
                                Cancel
                            </v-btn>
                            <v-btn
                                color="blue darken-1"
                                text
                                @click="save"
                            >
                                Save
                            </v-btn>
                            </v-card-actions>
                        </v-card>
                        </v-dialog>
                        <v-dialog v-model="dialogDelete" max-width="500px">
                        <v-card>
                            <v-card-title class="text-h5">Are you sure you want to delete this item?</v-card-title>
                            <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" text @click="closeDelete">Cancel</v-btn>
                            <v-btn color="blue darken-1" text @click="deleteItemConfirm">OK</v-btn>
                            <v-spacer></v-spacer>
                            </v-card-actions>
                        </v-card>
                        </v-dialog>
                    </v-toolbar>
                    </template>
                    <template v-slot:[`item.actions`]="{ item }">
                    <v-icon
                        small
                        class="mr-2"
                        @click="editItem(item)"
                    >
                        mdi-pencil
                    </v-icon>
                    <v-icon
                        small
                        @click="deleteItem(item)"
                    >
                        mdi-delete
                    </v-icon>
                    </template>
                    <template v-slot:no-data>
                        
                    </template>
                </v-data-table>
            </v-card>
        </div>
    </div>
</template>

<script>
import TitleWithSharp from '../MiscellaneousWidgets/TitleWithSharp.vue'
export default {
  components: { TitleWithSharp },
    data(){
        return{
            dialog: false,
            dialogDelete: false,
            headers: [
                {
                text: this.$t('community.uc.cr.ord'),
                align: 'start',
                sortable: false,
                value: 'name',
                },
                { text: this.$t('community.uc.cr.id'), value: 'id' },
                { text: this.$t('community.uc.cr.name'), value: 'name' },
                { text: this.$t('community.uc.cr.level'), value: 'level' },
                { text: this.$t('community.uc.cr.time'), value: 'time' },
                { text: 'Actions', value: 'actions', sortable: false },
            ],
            creditHistory: [],
            editedIndex: -1,
            editedItem: {
                name: '',
                calories: 0,
                fat: 0,
                carbs: 0,
                protein: 0,
            },
            defaultItem: {
                name: '',
                calories: 0,
                fat: 0,
                carbs: 0,
                protein: 0,
            },
        }
    },

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
    },

    created () {
      this.initialize()
    },

    methods: {
      initialize () {
        this.creditHistory = [
          
        ]
      },

      editItem (item) {
        this.editedIndex = this.creditHistory.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        this.editedIndex = this.creditHistory.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true
      },

      deleteItemConfirm () {
        this.creditHistory.splice(this.editedIndex, 1)
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.creditHistory[this.editedIndex], this.editedItem)
        } else {
          this.creditHistory.push(this.editedItem)
        }
        this.close()
      },
    },
  }
</script>

